import React, { Fragment, useState } from "react";
import "./App.css";

import Section from "./Components/Section/Section";

// Task2
import Phonebook from "./Components/Phonebook/Phonebook";
import Contacts from "./Components/Contacts/Contacts";
import Filter from "./Components/Filter/Filter";

const App = () => {
  const [contacts, setContacts] = useState([]);
  const [filteredContacts, setFilteredContacts] = useState([]);
  const [filter, setFilter] = useState("");

  const addContact = (contact) => {
    if (contacts.find((item) => item.name === contact.name)) {
      alert(`${contact.name} id already in contacts`);
      return;
    }
    setContacts((prev) => [...prev, contact]);
  };
  const inputHeandlerFilter = ({ target }) => {
    setFilter(target.value);
    const newArray = contacts.filter((contact) => contact.name.includes(target.value));
    setFilteredContacts(newArray);
  };
  const removeContact = (id) => {
    setContacts((prev) => prev.filter((contact) => contact.id !== id));
    if (filteredContacts.length > 0) {
      setFilteredContacts((prev) => prev.filter((contact) => contact.id !== id));
    }
  };
  const resetInput = () => {
    setFilter("");
    setFilteredContacts([]);
  };
  return (
    <Fragment>
      <Section title="Phonebook">
        <Phonebook addContact={addContact} />
      </Section>
      <br />
      <Section title="Contacts">
        <Filter inputHeandlerFilter={inputHeandlerFilter} filter={filter} resetInput={resetInput} />
        <Contacts contacts={filteredContacts.length > 0 ? filteredContacts : contacts} removeContact={removeContact} />
      </Section>
    </Fragment>
  );
};

export default App;
